.tableFooter {
    display: grid;
    grid-template-columns: 1fr 350px;
    margin-top: 4em;
}

.total {}

.total p {
    padding: 0 2em;
    display: flex;
    justify-content: space-between;

}

.total p:last-child {
    background: #cfd4d5;
    padding-top: 8px;
    padding-bottom: 8px;
}

.addresses {
    margin-top: 5em;
    display: grid;
    grid-template-columns: repeat(2, 1fr);

}